* {
  margin: 0;
}

body {
  background-color: rgb(231, 231, 229);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.header {
    height: 60px;
    display: flex;
    align-items: center;
    background-color: #131212;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 100;
}
.header__search {
    display: flex;
    flex: 1 1;
    align-items: center;
    border-radius: 24px;
    border-radius: 24px;
}
.header__searchInput {
    height: 22px;
    border: none;
    width: 100%;
}
.header__searchInput:focus {
    outline:none !important ;   
}
.header__logo {
    width: 100px;
    object-fit:contain;
    margin: 0 20px;
    margin-top: 18px;
}
.header__searchIcon {
    background-color: rgb(208, 155, 41);
    height: 22px;
    padding: 5px;
}
.header__nav {
    display: flex;
    justify-content: space-evenly;
}
.header__optionLineOne {
    font-size: 10px;
}
.header__optionLineTwo {
    font-size: 13px;
    font-weight: 800;
}
.header__option {
    display: flex;
    color: white;
    flex-direction: column;
    margin: 0 10px 0 10px;
}
.header__optionBasket {
    display: flex;
    align-items: center;
    font-size: 15px;
    font-weight: 800;
    color: white;
    margin-right: 10px;
}


.home {
  display: flex;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  max-width: 1300px;
}
.home__row {
  display: flex;
  z-index: 1;
  margin-left: 5px;
  margin-right: 5px;
}
.home__image {
  width: 100%;
  z-index: -1;
  margin-bottom: -150px;
  -webkit-mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
          mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
}

.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: rgb(255, 255, 255);
    position: relative;
    z-index: 1;
}
.product__rating {
    display: flex;
}
.product > img {
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
    margin-top: 100px;
}

.product > button {
    background-color: #ca9d17;
    border: 1px solid;
    margin-top: 10px;
    border-color: #e9ba2f #b1902b #a88622;
    color: #111;
}

.product__price {
    margin-top: 5px;
}

.product__info {
    height: 100px;
    margin-bottom: 20px; 
    position: absolute;
    top: 5px;
    left: 5px;
}
.checkout {
    display: flex;
    background-color: white;
    padding: 20px;
    height: -webkit-max-content;
    height: max-content;

}

.checkout__ad {
    width: 100%;
    margin-bottom: 10px;
}

.checkout__title {
    margin-right: 10px;
    padding: 10px;
    border-bottom: 1px solid lightgray;
}

